.insta {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 10px;

    img {
        width: 100%;
        border-radius: 5px;
    }

}


.insta-item {
    flex: 0 0 calc(33% - 5px);
}

@media all and (max-width: $breaking-point-phone) {
    .insta {
        flex-direction: column;
    }

    .insta-item {
        flex: 1;
    }
}
@media all and (max-width: $breaking-point-tablet) {
    .insta-item {
        flex: 0 0 calc(50% - 5px);;
    }
}

